.header__logo {
    max-width: 142px;
    min-height: 33px;
    margin-bottom: 41px;
}


@media screen and (max-width: 768px) {
    .header__logo { 
      max-width: 115px;
      min-height: 28px;
      margin-bottom: 35px;
    }  
}

